import * as React from 'react';
import ReactEcharts from 'echarts-for-react';
import echarts from 'echarts';

export default class BackPage extends React.Component<any, any> {
  constructor(props: any) {
    super(props);
    this.state = {
      histogramData: {}
    }
  }
  componentDidMount() {
    this.getOption();
  }
  getOption (date: string = '2019-01-20') {
    fetch(`http://localhost:2827/back/data/${date}`, {
      method: "GET",
      headers: {
        "Content-Type": "application/json"
      },
    })
    .then(res => res.json())
    .then(res => {
      console.log(res);
      this.setState({
        histogramData: {
          backgroundColor: '#fff',
          tooltip: {
              trigger: 'axis',
              axisPointer: {
                  type: 'shadow'
              }
          },
          legend: {
              data: ['飞机场', '火车站', '高铁站'],
              align: 'right',
              right: 10,
              textStyle: {
                  color: "#00265f"
              },
              itemWidth: 10,
              itemHeight: 10,
              itemGap: 35
          },
          grid: {
              left: '3%',
              right: '4%',
              bottom: '3%',
              containLabel: true
          },
          xAxis: [{
              type: 'category',
              data: res.data.times,
              axisLine: {
                  show: true,
              },
              axisTick: {
                  show: false,
              },
              axisLabel: {
                  show: true
              },
          }],
          yAxis: [{
              type: 'value',
              axisLabel: {
                  formatter: '{value}'
              },
              axisTick: {
                  show: false,
              },
              axisLine: {
                  show: false,
              },
          }],
          series: [{
              name: '飞机场',
              type: 'bar',
              data: res.data.plane,
              barWidth: 15,
              barGap: 1,
          }, {
              name: '火车站',
              type: 'bar',
              data: res.data.train,
              barWidth: 15,
              barGap: 1,
          }, {
              name: '高铁站',
              type: 'bar',
              data: res.data.gaotie,
              barWidth: 15,
              barGap: 1,
          }]
        }
      })
    });
  }
  render() {
    return (
      <>
        <ReactEcharts
          style={{minHeight: 600, background: '#fff', paddingTop: 12}}
          option={this.state.histogramData}
          notMerge={true}
          lazyUpdate={true}
        />
      </>
    )
  }
}
